/** @jsx jsx */
import { css, jsx } from '@emotion/core';
import { GithubFilled, LinkedinFilled } from '@ant-design/icons';

const Info = ({ firstName, lastName, role, linkedIn, github }) => (
  <div
    css={theme => css`
      height: 100%;
      padding: 1.875em;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      font-family: Epilogue;
      @media (max-width: ${theme.breakpoints.sm}) {
        flex-direction: row;
        padding: 1.25em;
      }
    `}
  >
    <div>
      <div
        css={theme => css`
          opacity: 0.95;
          font-weight: 600;
          line-height: 1.08;
          letter-spacing: 1.3px;

          span {
            font-size: 3em;
            display: block;
          }

          @media (max-width: ${theme.breakpoints.sm}) {
            span {
              display: inline;
              font-size: 1rem;
            }

            .last-name::before {
              content: ' ';
            }
          }
        `}
      >
        <span className="first-name">{firstName}</span>
        <span className="last-name">{lastName}</span>
      </div>
      <div
        css={theme => css`
          margin-top: 0.5em;
          opacity: 0.9;
          font-weight: 300;
          letter-spacing: 0.32px;
          @media (max-width: ${theme.breakpoints.sm}) {
            font-size: 0.75em;
          }
        `}
      >
        {role}
      </div>
    </div>
    <div
      css={theme => css`
        flex-shrink: 0;
        font-size: 2em;
        text-align: right;

        a {
          text-decoration: none;
          color: inherit;
          opacity: 0.7;

          &:hover {
            opacity: 1;
          }
        }

        > *:not(:first-of-type) {
          margin-left: 1rem;
        }

        @media (max-width: ${theme.breakpoints.sm}) {
          font-size: 1em;
        }
      `}
    >
      <a href={linkedIn} target="_blank" rel="noopener noreferrer">
        <LinkedinFilled />
      </a>
      {github && (
        <a href={github} target="_blank" rel="noopener noreferrer">
          <GithubFilled />
        </a>
      )}
    </div>
  </div>
);

const ProfileCard = ({
  firstName,
  lastName,
  role,
  photo,
  linkedIn,
  github,
}) => {
  const info = (
    <Info
      firstName={firstName}
      lastName={lastName}
      role={role}
      linkedIn={linkedIn}
      github={github}
    />
  );
  return (
    <div
      className="profile-card"
      css={css`
        position: relative;
      `}
    >
      <div
        css={theme => css`
          width: 100%;
          height: 100%;
          background-image: url(${photo});
          background-repeat: no-repeat;
          background-size: cover;
          background-position: center;

          .img {
            display: none;
            width: 100%;
          }

          @media (max-width: ${theme.breakpoints.sm}) {
            height: initial;
            background: none;

            .img {
              display: block;
            }
          }
        `}
      >
        <img src={photo} className="img" alt="Profile" />
      </div>
      <div
        className="info"
        css={theme => css`
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          background: rgba(0, 0, 0, 0.6);
          transition: all 0.2s;
          opacity: 0;

          .profile-card:hover & {
            opacity: 1;
          }

          @media (max-width: ${theme.breakpoints.sm}) {
            position: static;
            opacity: 1;
            background: none;
          }
        `}
      >
        {info}
      </div>
    </div>
  );
};

export default ProfileCard;
